'use client';
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";

export default function Hero() {
  return (
    <section id="home" className="pt-24 md:pt-32 pb-16 md:pb-24 relative overflow-hidden">
      <div className="absolute inset-0 z-0">
        <div className="absolute inset-0 bg-gradient-to-r from-blue-50 via-blue-50 to-transparent z-10"></div>
        <img 
          src="https://s3.us-east-1.amazonaws.com/images.gearjunkie.com/uploads/2024/04/LinkFace.jpg" 
          alt="Dog wearing smart belt" 
          className="w-full h-full object-cover object-top"
        />
      </div>
      
      <div className="container mx-auto px-4 relative z-20">
        <div className="max-w-xl">
          <h1 className="text-4xl md:text-5xl font-bold text-gray-800 mb-4">Make your pets smarter</h1>
          <p className="text-lg text-gray-600 mb-8">India’s first smart belt that monitors your pet's health and real-time location, even when you're away.</p>
          <div className="flex flex-col sm:flex-row gap-4">
            <Button onClick={() => scrollToSection('booking')} className="bg-blue-600 hover:bg-blue-700 text-white px-8 py-6 text-lg !rounded-button cursor-pointer whitespace-nowrap">
              Join Waitlist <i className="fa-solid fa-arrow-right ml-2"></i>
            </Button>
            <Button onClick={() => scrollToSection('features')} variant="outline" className="border-blue-600 text-blue-600 hover:bg-blue-50 px-8 py-6 text-lg !rounded-button cursor-pointer whitespace-nowrap">
              Explore Features
            </Button>
          </div>
          <div className="mt-8 flex items-center">
            <Badge className="bg-blue-100 text-blue-800 px-3 py-1 !rounded-button">
              <i className="fa-solid fa-clock mr-2"></i> Limited Time Offer
            </Badge>
            <p className="ml-3 text-gray-700">
              Join<span className="font-semibold"> 447 </span>pet lovers on the waitlist
            </p>
          </div>
        </div>
      </div>
    </section>
  );

  function scrollToSection(id: string) {
    const element = document.getElementById(id);
    if (element) element.scrollIntoView({ behavior: 'smooth' });
  }
}